<template>
<div>
      <van-row type="flex" justify="center" align="center" style="margin-top:20px"  >
        <van-col span="24" style="text-align: center;">
            <van-image
                  round
                  fit="cover"
                  width="10rem"
                  height="10rem"
                  src="/image/NO1.jpg"
            />
        </van-col>
      </van-row>
      <van-row type="flex" justify="center" align="center" style="margin-top:20px" >
            <van-col span="24">
                <!-- 密码输入框 -->
                <van-password-input
                  :value="value"
                  info="密码为 6 位数字"
                  :focused="showKeyboard"
                  @focus="showKeyboard = true"
                />
            </van-col>
      </van-row>
  
  
  

  <!-- 数字键盘 -->
  <van-number-keyboard
    :show="showKeyboard"
    @input="onInput"
    @delete="onDelete"
    @blur="showKeyboard = false"
  />
</div>
</template>
<script>
export default {
  name: 'login',
  components: {
  },
  data() {
    return {
      value: '',
      showKeyboard: false
    };
  },

  methods: {
    onInput(key) {
      this.value = (this.value + key).slice(0, 6);
      if(this.value.length === 6){
        if(this.value === '199518'){
          this.$router.push({path:'/home'})
        }else{
          this.$toast("你丫的好好想想密码是啥！！")
          this.value=''
        }
      }
      
    },
    onDelete() {
      this.value = this.value.slice(0, this.value.length - 1);
    }
  }
}
</script>
<style>
.margin {
  margin: 10px 0px;
}
</style>
